import { createSlice } from '@reduxjs/toolkit'

export const pageSlice = createSlice({
  name: 'page',
  initialState: {
    value: 2,
  },
  reducers: {
    add: (state) => {
      state.value+=1;
    }
  },
})

// Action creators are generated for each case reducer function
export const { add } = pageSlice.actions

export default pageSlice.reducer
