import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import React, { useEffect, useLayoutEffect, useRef, useState} from 'react';
import { useSelector } from 'react-redux';
import "./ScrollTrigger.css";
import ReadText from "./GetText";
import Title from "./storyComponents/1title";

const sleep = time => new Promise(resolve => setTimeout(resolve, time))
class TypeAsync extends HTMLSpanElement {
    get typeInterval() {
        const randomMs = 100 * Math.random()
        return randomMs < 50 ? 10 : randomMs
    }

    async type(text) {
        for (let character of text) {
            this.innerText += character
            await sleep(this.typeInterval)
        }
    }
}

customElements.define('type-async', TypeAsync, { extends: 'span' })

const ScrollTriggerWrapper = () => {
    const app = useRef();
    const tl = useRef();
    const tl2 = useRef();
    const articleRef = useRef([]);
    const page = useSelector((state) => state.page.value);
    const [mouseValue, updateMouseValue] = useState(0);
    const mouseXUpdate = useSelector((state) => state.mouseChanged.value);
    const textTypingRef = useRef();


    const wrapperDivsRef = useRef([]);

    gsap.registerPlugin(ScrollTrigger);



    async function addElement() {

        const languageIndex = Math.floor(Math.random() * 136);
        const paragraphIndex = Math.floor(Math.random() * 70);
        const stringContent = await ReadText(languageIndex, paragraphIndex);
        // await textTypingRef.current.type(stringContent);
        // create a new div element
        const wrapperDiv = document.createElement("div");
        wrapperDiv.setAttribute("id", page);
        wrapperDiv.classList.add("wrapper");

        // and give it some content
        const vlDiv = document.createElement("div");
        vlDiv.classList.add("vl");

        const boundingBoxDiv = document.createElement("div");
        boundingBoxDiv.classList.add("boundingBox");
        const textDiv = document.createElement("div");
        textDiv.classList.add("text");
        const textContent = document.createTextNode(stringContent);
        textDiv.appendChild(textContent);
        boundingBoxDiv.appendChild(textDiv);
        wrapperDiv.appendChild(vlDiv);
        wrapperDiv.appendChild(boundingBoxDiv);
        const titleDiv = document.createElement("button");
        boundingBoxDiv.onclick = ()=>{console.log("appending divs");wrapperDiv.appendChild(titleDiv)};
        const currentDiv = document.getElementById("bamboo");
        currentDiv.appendChild(wrapperDiv);
        wrapperDivsRef.current.push(wrapperDiv);
        let positionYInfo = (60 * page + window.innerHeight * 0.3).toString() + "px";

        tl.current += gsap.timeline({ duration: 0.9, onComplete: updateNewInstanceAnimation })
            .from(wrapperDiv, { opacity: 0, y: "-10vh" }, 0.)
            .to(wrapperDiv, { css: { height: positionYInfo } }, 0.)
            .from(boundingBoxDiv, { y: "-30vh", x: "-=13rem" }, 0.)
            .from(textDiv, { x: "+=13rem", opacity: 0 }, 0.)

    }
    const updateNewInstanceAnimation = () => {
        let m = window.innerWidth * mouseXUpdate;
        tl.current += gsap.timeline({ duration: 0.3, })
            .to(wrapperDivsRef.current[page - 3], { x: m.toString() })
    }

    const updateAnimation = (nowMouseValue) => {
        if (page > 4) {
            let stopIndex = wrapperDivsRef.length < page - 3 ? page - 1 : page;
            for (let i = 3; i <= stopIndex; i++) {
                let f;
                let m;
                f = 1.57 - i / page * 1.57;
                let windowRatio = window.innerWidth / window.innerHeight;
                m = window.innerWidth * (Math.max(-0.43 / windowRatio, Math.min(nowMouseValue, 0.43 / windowRatio)) * (1. - (f / 1.57)) + f * f * 0.5 / windowRatio);
                if (wrapperDivsRef.current[i - 3]) {
                    gsap.set(wrapperDivsRef.current[i - 3], { x: m.toString() })
                }

            }

        }

    }



    const handleMouseMove = (e) => {
        
        let nowMouseValue = (e.pageX / window.innerWidth - 0.5);
        console.log("on mouse move",nowMouseValue)
        updateMouseValue(nowMouseValue)
        updateAnimation(nowMouseValue);
    }


    useEffect(() => {
        if (page > 2) {
            addElement();
        }
    }, [page])

    return (
        <div className="hero" onMouseMove={handleMouseMove} style={{ width: "100%", height:"100%"}}>

            <div id="bamboo" className="bamboo" ref={app}>

            </div>
            <div className="onTop">
                <svg
                    id="Layer_1"
                    viewBox="0 0 265.9 379.1"
                >
                    <clipPath id="book" transform="scale(3 3) translate(-200 0)">
                        <path id="bookShape" d="M506.2,310.5c-0.7-88.3-1.3-176.7-2-265c-17.7-9.8-55.8-27.8-107-28c-41.6-0.1-74.1,11.5-92.9,20.1H291
		c-18.8-8.6-51.3-20.2-92.9-20.1c-51.2,0.2-89.3,18.2-107,28c-0.7,88.3-1.3,176.7-2,265c19.9-11.4,62.2-32.1,119-32
		c35.2,0.1,63.8,8.1,82.9,15.4v6.6h15.1v-7.3c19.1-7.1,47-14.6,81.1-14.7C443.9,278.4,486.3,299.1,506.2,310.5z"/>
                    </clipPath>
                </svg>
            </div>
            <div className="backgroundTexts">
                {/* <div className="clip">
                    <h3 ref={el => articleRef[0] = el}>
                        <span is="type-async" id="type-text" ref={textTypingRef} ></span>
                    </h3>
                </div> */}
                <div className="background">
                    
                </div>
            </div>
        </div>

    )
}

export default ScrollTriggerWrapper;
