

import { useEffect, useRef } from "react";
import * as THREE from "three";
import { useDispatch} from 'react-redux';
import { add } from "../reducer/pageReducer";
import { update } from "../reducer/mouseReducer";
import { mouseStateChanged } from "../reducer/mouseChangedReducer";

const GlslCanvas = () => {
    const uniforms = useRef();
   
    
    const canvas = useRef();
    const dispatch = useDispatch();
    let previousMouseX=0.;
    let previousUpdatedMouseX=0.;
    let fadeSpeed = 1.;
     
    const addOnePage = () => {
        uniforms.current.pageNumber.value += 1;
        dispatch(add());
        
        fadeSpeed= -3. - fadeSpeed;  //1.,-4.
        if (uniforms.current.fade.value <= 0.) {
            uniforms.current.fade.value = 0.;
        }
        if (uniforms.current.fade.value > 5.) {
            uniforms.current.fade.value = 5.;
        }
    }
    let camera, scene, renderer, clock, loader;
    useEffect(() => {
       
        
       

        
      
        const render = () => {
            uniforms.current.u_time.value += clock.getDelta();
            renderer.render(scene, camera);
        }

        const onWindowResize = (event) => {

            renderer.setSize(window.innerWidth, window.innerHeight);
            uniforms.current.u_resolution.value.x = renderer.domElement.width;
            uniforms.current.u_resolution.value.y = renderer.domElement.height;
        }

        const init = () => {
            camera = new THREE.Camera();
            camera.position.z = 1;
            scene = new THREE.Scene();
            loader = new THREE.TextureLoader();
            clock = new THREE.Clock();
            let geometry = new THREE.PlaneGeometry(2, 2);
            uniforms.current = {
                u_time: { type: "f", value: 1.0 },
                u_resolution: { type: "v2", value: new THREE.Vector2() },
                u_mouse: { type: "v2", value: new THREE.Vector2() },
                iChannel0: { type: "t", value: loader.load("texture.png") },
                pageNumber: { type: "i", value: 2 },
                fade: { type: "f", value: 0. }
            };
            let material = new THREE.ShaderMaterial({
                uniforms: uniforms.current,
                vertexShader: document.getElementById('vertexShader').textContent,
                fragmentShader: document.getElementById('fragmentShader').textContent
            });
            let mesh = new THREE.Mesh(geometry, material);
            scene.add(mesh);
            renderer = new THREE.WebGLRenderer();
            renderer.setPixelRatio(window.devicePixelRatio);
            canvas.current.appendChild(renderer.domElement);
            onWindowResize();
            window.addEventListener('resize', onWindowResize, false);
            document.addEventListener("mousemove",handleMouseMove,false);
        }

        const animate = () => {
            requestAnimationFrame(animate);
            render();
        }

  
        init();
        animate();

    }, []);
    let timeOut;
        let nowMouseValue;
    const handleMouseMove=(e)=>{
        nowMouseValue = e.pageX / window.innerWidth - 0.5;
        clearTimeout(timeOut);
        timeOut = setTimeout(function () { dispatch(mouseStateChanged(nowMouseValue)); }, 50)
        dispatch(update(nowMouseValue));
        if (Math.abs(previousUpdatedMouseX - nowMouseValue) > 0.03) {
            dispatch(mouseStateChanged(nowMouseValue));
            previousUpdatedMouseX=previousMouseX;
        }
        if (previousMouseX >= 0 && nowMouseValue < 0) {
            addOnePage();
        }
      
        
        previousMouseX = nowMouseValue;
        uniforms.current.u_mouse.value.x = e.pageX / window.innerWidth * uniforms.current.u_resolution.value.x;
        uniforms.current.u_mouse.value.y = e.pageY / window.innerHeight * uniforms.current.u_resolution.value.y;
        if (uniforms.current.fade.value >= 0.) {
            uniforms.current.fade.value += fadeSpeed * clock.getDelta();
        }
    }
  
    return (

        <div id="container" ref={canvas} >
            
        </div>


    )
}

export default GlslCanvas