const fetchData = (languageIndex,paragraphIndex) => {
    return fetch(`./files/lan.json`
        , {
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        })
        .then((response) => response.json())
        .then(function (myJson) {

            const language = myJson[languageIndex].code;

            return fetch(`./files/${language}.json`
                , {
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json'
                    }
                }
            )
                .then((response) => response.json())
                .then(function (myJson) {
                    return myJson[paragraphIndex]
                });
        })
}

const ReadText = async (languageIndex,paragraphIndex) => {
    const a = await fetchData(languageIndex,paragraphIndex);
    return a;
}

export default ReadText

