import { createSlice } from '@reduxjs/toolkit'

export const mouseChangedSlice = createSlice({
  name: 'mouse trigger animation',
  initialState: {
    value: 0,
  },
  reducers: {
    mouseStateChanged: (state, action) => {
        
      state.value = action.payload;
    }
  },
})

// Action creators are generated for each case reducer function
export const { mouseStateChanged } = mouseChangedSlice.actions

export default mouseChangedSlice.reducer
