import { configureStore } from '@reduxjs/toolkit'
import pageReducer from '../reducer/pageReducer'
import mouseReducer from '../reducer/mouseReducer'
import clickReducer from '../reducer/clickReducer'
import mouseChangedReducer from '../reducer/mouseChangedReducer'
import stateReducer from '../reducer/stateReducer'
export default configureStore({
  reducer: {
    page: pageReducer,
    mouse: mouseReducer,
    click: clickReducer,
    mouseChanged: mouseChangedReducer,
    status: stateReducer
  },
})